.app__about{
    float: 1;
    width: 100%;
    flex-direction: column;
}
.app__profiles{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top: 2rem;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);

}
.app__profile-item{
    width: 190px;
    justify-content: flex-start;
    display: flex;
    flex-direction: column;
    display: flex;
    margin: 2rem;
    img{
        width: 100%;
        height: 170px;
        border-radius: 15px;
        object-fit: cover;
    }
    @media screen and (min-width:2000px) 
    {
        width: 370px;
        margin: 2rem 4rem;
        img{
            height: 320px;
        }
        
    }
}